import Frame from './Frame';
import Render from './Render';

/*
 * @description: AnimateAnimateapi
 * @author: Changle
 * @update: Changle (2016-10-30 23:00)
 */
class Scroll {
    constructor () {
        //
        this.option = this.option.bind(this);
        this.calculate = this.calculate.bind(this);
        this.render = this.render.bind(this);
        this.play = this.play.bind(this);
        //
        this.Frame = new Frame();
        this.Render = new Render();
        //domscrollbodyscroll
        this.elName = ''; this.el = null; this.containerEl = document.body;
        //
        this.targetPosition = 0; this.currentPosition = 0; this.offset = 0;
        //
        this.frameArr = []; this.frameCount = 0;
        //
        this.positiveForward = false;
    }
    //
    option (input) {
        ///dom
        [this.elName, this.time] = [
            input.elName,
            input.time,
        ];
        this.calculate();
        this.render();
    }
    //
    calculate () {
        //
        this.position();
        //
        this.frameCount = Math.ceil((this.time / 1000) * 60);
        //Frame
        this.Frame.option({
            frameCount: this.frameCount,
            offset: this.offset,
            animationType: 'sineAnimation',
        });
        //
        this.frameArr = this.Frame.frameArr;
    }
    //
    position () {
        this.containerEl = document.body;
        this.el = document.querySelector(this.elName);
        this.targetPosition = this.el.offsetParent.offsetTop + this.el.offsetTop;
        this.currentPosition = this.containerEl.scrollTop;
        this.offset = this.currentPosition - this.targetPosition;
    }
    //
    render () {
        //
        const func = (progress) => {
            this.containerEl.scrollTop = progress;
        }
        //Render
        this.Render.option({
            func: func,
            targetPosition: this.targetPosition,
            currentPosition: this.currentPosition,
            frameArr: this.frameArr,
            frameCount: this.frameCount,
        });
    }
    //
    play () {
        this.Render.action();
    }
}

export default Scroll;


