//
import { Parent, Func, Api } from './Base';

/*
 * @description: Articelcurd
 * @author: Changle
 * @update: Changle (2016-10-20 20:32)
 */
class Article {
    static createPost (page) {
        //id
        const pageId = page.pageId;
        //
        const title = page.title;
        //
        const content = page.content;
        //
        if (Func.empty(Func.trim(content))) {
            console.log('OoO');
            return false;
        }
        //
        const tags = page.tags;
        //
        const post = {
            pageId: pageId,
            title: title,
            content: content,
            tags: tags,
        };
        //
        const api = new Api({
            pageId: pageId,
        });
        const url = api.createPost;
        Parent.post(url, post, (res) => {
            console.log(res);
        });
    }

    /*
     * @description: 
     * @subscription: viewpost
     * @callback: 
     */
    static getPost (page) {
        //id
        const postId = page.$route.params.id;
        //
        const api = new Api({ postId: postId });
        const url = api.getPost;
        //get
        Parent.get(url, '', (res) => {
            if (res.success) {
                page.post = res.data;
            }
        });
    }

    /*
     * @description: 
     * @subscription: viewposts
     * @callback: 
     */
    static getPosts (page) {
        //id
        const pageName = page.name;
        //
        const api = new Api({ pageName: pageName });
        const url = api.getPosts;
        //get
        Parent.get(url, '', (res) => {
            if (res.success) {
                page.posts = res.data;
            }
        });
    }
}

export default Article;


